@import './variables.scss';
@import './remove.scss';

@import './components/header.scss';
@import './components/sneakers.scss';
@import './components/card.scss';


* {
    font-family: 'Inter', sans-serif;
    font-size: 16px;
}
body {
    margin: 0;
    padding: 0;
    background-color: $background-color;
    padding: 85px;
    color: $black-color;
    box-sizing: border-box;
    @media (max-width: 768px) {
        padding: 45px;
    }
    @media (max-width: 576px) {
        padding: 20px;
    }
}
.wrapper {
    background-color: $white-color;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: $border-radius;
}


.banner {
    padding: 45px;

    @media (max-width: 576px) {
        padding: 25px;
    }
    &__slider {
        width: 100%;
        height: 300px;
        background: #F4EFE9;
        border-radius: $border-radius;
    }
}
.products {
    padding: 45px;

    @media (max-width: 576px) {
        padding: 25px;
    }
    &__header {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 20px;
        margin-bottom: 40px;
    }
    &__title {
        font-size: 32px;
        font-weight: 700;
        line-height: 39px;
        margin-right: auto;
    }
}
.search {
    display: flex;
    align-items: center;
    gap: 10px;
    border-radius: 10px;
    border: 1px solid #F3F3F3;
    padding: 15px 18px;

    &__input {
        width: 100%;
        max-width: 200px;
    }
}

