.sneakers {
   display: grid;
   grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
   gap: 40px;

   &__block {
		position: relative;
      border-radius: 40px;
      border: 2px solid #f3f3f3;
      padding: 30px;
		transition: all 0.3s ease 0s;

		&:hover {
			box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.05);
			transform: translateY(-5px);
		}
   }
   &__img {
      width: 100%;
      height: auto;
      margin-bottom: 15px;
   }
   &__title {
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      margin-bottom: 20px;
   }
   &__like {
		position: absolute;
		top: 30px;
		left: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
      border: 2px solid #f8f8f8;
		background-color: $white-color;
      border-radius: 7px;
		width: 34px;
		height: 34px;
		transition: all 0.3s ease 0s;
		cursor: pointer;

		svg {
			fill: none;
		}
		svg > path {
			fill: #ECECEC;
		}
   }
	&__like:hover,
	&__like.active {
		background-color: #FEF0F0;
		border: 2px solid #FEF0F0;
		svg > path {
			fill: #FF8585;
		}
	}
}
.price {
   display: flex;
   align-items: center;
   gap: 10px;

   &__content {
      display: flex;
      flex-direction: column;
      margin-right: auto;
   }
   &__title {
      font-size: 14px;
      font-weight: 500;
      line-height: 13px;
      text-transform: uppercase;
      color: #bdbdbd;
      margin-bottom: 10px;
   }
   &__cost {
      font-size: 16px;
      font-weight: 700;
      line-height: 17px;
   }
}
.add {
   padding: 10px 13px;
   border-radius: 10px;
   border: 2px solid #f3f3f3;
   transition: all 0.3s ease 0s;
   cursor: pointer;

   &:hover {
      background-color: #f3f3f3;
   }
}
