h1, h2, h3, h4, h5, h6 {
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    padding: 0;
}
p {
    margin: 0;
    padding: 0;
}
input,
input:focus,
input:hover,
input:active {
    border: none;
    outline: none;
}
button {
    border: none;
    outline: none;
    background-color: transparent;
}