.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}
.drawer {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    width: 385px;
    height: 100%;
    background-color: $white-color;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
    padding: 30px;
    
    box-sizing: border-box;
    
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }
    &__title {
        font-size: 24px;
        font-weight: 700;
        line-height: 29px;
        margin-right: auto;
    }
    &__block {
        display: flex;
        flex-direction: column;
        gap: 20px;
        height: 100%;
        overflow: auto;
        margin-bottom: 30px;
    }
    &__close {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        width: 32px;
        height: 32px;
        transition: all 0.3s ease 0s;
        cursor: pointer;

        svg > path {
            fill: $black-color;
            transition: all 0.3s ease 0s;

            &:hover {
               fill: #B5B5B5;
            }
        }     
    }
}
.card-item {
    display: flex;
    align-items: center;
    gap: 20px;
    border: 2px solid #F3F3F3;
    border-radius: 20px;
    padding: 20px;

    &__img {
        width: 70px;
    }
    &__content {
        display: flex;
        flex-direction: column;
    }
    &__title {
        font-size: 14px;
        line-height: 17px;   
        margin-bottom: 8px;     
    }
    &__cost {
        font-size: 14px;
        font-weight: 700;
        line-height: 17px;
    }
    &__delete {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0 0 32px;
        border: 2px solid #DBDBDB;
        border-radius: 8px;
        width: 32px;
        height: 32px;
        transition: all 0.3s ease 0s;
        cursor: pointer;
     
        &:hover {
           background-color: #f3f3f3;
        }
    }
}