.header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 30px;
    padding: 45px;
    border-bottom: 1px solid #EAEAEA;

    @media (max-width: 576px) {
        padding: 25px;
    }
}
.logo {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-right: auto;

    &__img {
        width: 40px;
        height: 40px;
    }
    &__title {
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        text-transform: uppercase;

        @media (max-width: 1200px) {
            font-size: 18px;
        }
    }
    &__description {
        font-size: 14px;
        font-weight: 400;
        line-height: 17px;
        color: #9D9D9D;
        @media (max-width: 1200px) {
            font-size: 12px;
        }
    }
}
.user {
    display: flex;
    align-items: center;
    gap: 30px;
    @media (max-width: 1200px) {
        gap: 20px;
    }

    &__curt {
        display: flex;
        align-items: center;
        gap: 10px;
    }
    &__cost {
        font-size: 14px;
        font-weight: 600;
        line-height: 17px;
        color: #5C5C5C;
    }
    &__favorite,
    &__account {
        width: 20px;
        height: 20px;
    }
}